"use client"

import { useEffect, useState } from "react"
import { Button } from "@/components/ui/button"
import { useToast } from "@/hooks/use-toast"
import BlogForm from "./blog-form"
import BlogList from "./blog-list"

export default function BlogManager() {
  const [blogs, setBlogs] = useState([])
  const [showForm, setShowForm] = useState(false)
  const [editingBlog, setEditingBlog] = useState(null)
  const [loading, setLoading] = useState(true)
  const { toast } = useToast()

  useEffect(() => {
    fetchBlogs()
  }, [])

  const fetchBlogs = async () => {
    try {
      const response = await fetch("/api/admin/blogs")
      const data = await response.json()
      setBlogs(data)
    } catch (error) {
      toast({ title: "Error", description: "Failed to fetch blogs", variant: "destructive" })
    } finally {
      setLoading(false)
    }
  }

  const handleDelete = async (id: string) => {
    if (!confirm("Are you sure you want to delete this blog?")) return

    try {
      const response = await fetch(`/api/admin/blogs/${id}`, { method: "DELETE" })
      if (response.ok) {
        setBlogs(blogs.filter((b) => b.id !== id))
        toast({ title: "Success", description: "Blog deleted" })
      }
    } catch (error) {
      toast({ title: "Error", description: "Failed to delete blog", variant: "destructive" })
    }
  }

 const handleSave = async (blog: any) => {
  try {
    // Normalize the data: convert string "0"/"1" to actual numbers
    const normalizedBlog = {
      ...blog,
      featured: blog.featured === "1" || blog.featured === true ? 1 : 0,
      status: blog.status === "1" || blog.status === true ? 1 : 0,
      // Optional: also ensure likes, stars are numbers
      likes: Number(blog.likes) || 0,
      stars: Number(blog.stars) || 0,
    };

    let response;
    if (editingBlog) {
      response = await fetch(`/api/admin/blogs/${blog.id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(normalizedBlog), // ← use normalized data
      });
    } else {
      response = await fetch("/api/admin/blogs", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(normalizedBlog), // ← use normalized data
      });
    }

    if (response.ok) {
      const savedBlog = await response.json();

      if (editingBlog) {
        setBlogs(blogs.map((b) => (b.id === savedBlog.id ? savedBlog : b)));
        toast({ title: "Success", description: "Blog updated successfully" });
      } else {
        setBlogs([...blogs, savedBlog]);
        toast({ title: "Success", description: "Blog created successfully" });
      }

      setShowForm(false);
      setEditingBlog(null);
    } else {
      throw new Error("Failed to save");
    }
  } catch (error) {
    console.error(error);
    toast({
      title: "Error",
      description: "Failed to save blog",
      variant: "destructive",
    });
  }
};

  if (loading) return <div>Loading...</div>

  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-3xl font-bold">Blog Posts</h2>
        <Button
          onClick={() => {
            setShowForm(true)
            setEditingBlog(null)
          }}
        >
          Add New Blog
        </Button>
      </div>

      {showForm && (
        <BlogForm
          blog={editingBlog}
          onSave={handleSave}
          onCancel={() => {
            setShowForm(false)
            setEditingBlog(null)
          }}
        />
      )}

      <BlogList
        blogs={blogs}
        onEdit={(blog) => {
          setEditingBlog(blog)
          setShowForm(true)
        }}
        onDelete={handleDelete}
      />
    </div>
  )
}
